section {
    width: 100%;

    .section_wrapper {
        max-width: 500px;
        margin: auto;
        padding: 0 2rem;
        margin-top: 150px;
        box-shadow: 0 0 8px gray;
        border-radius: 10px;

        .title {
            width: 100%;
            text-align: center;

            h1 {
                padding: 30px 0;
                font-family: Mulish;
                font-size: 24px;
                font-style: normal;
                font-weight: 700;
                line-height: normal;
                letter-spacing: 0.3px;
            }
        }

        form {
            width: 100%;

            p {
                padding: 20px 0 5px 0;
                color: rgb(90, 88, 88);
                font-family: Mulish;
                font-size: 15px;
                font-style: normal;
                font-weight: 700;
                line-height: normal;
                letter-spacing: 0.4px;

                span {
                    color: chocolate;
                    font-size: 15px;
                }

            }

            input {
                width: 100%;
                height: 30px;
                padding: 5px;
                margin: 8px 0 20px 0;
                border: 1px solid rgb(176, 175, 175);
                border-radius: 5px;
                outline: none;
                color: rgb(90, 88, 88);
                font-family: Mulish;
                font-size: 18px;
                font-style: normal;
                font-weight: 700;
                line-height: normal;
                letter-spacing: 0.4px;

                &::placeholder {
                    color: rgb(176, 175, 175);
                }
            }

            input:focus {
                border: 1px solid blue;
            }

        }

        button {
            width: 100%;
            height: 40px;
            margin: 30px 0 20px 0;
            border: none;
            border-radius: 5px;
            color: rgb(153, 153, 153);
            font-family: Mulish;
            font-size: 20px;
            font-style: normal;
            font-weight: 800;
            line-height: normal;
            letter-spacing: 5px;
            cursor: pointer;
        }

        p{
            text-align: center;
            padding-bottom: 20px;

            a{
                text-decoration: none;
            }
        }
    }
}