.overview {
  width: 100%;
  fill: var(--grayscale-bg, #F7F8FC);
}
.overview main {
  width: 100%;
}
.overview main .main_wrapper {
  max-width: 1122px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
  gap: 1rem;
}
.overview main .main_wrapper .card {
  width: 258px;
  border-radius: 8px;
  border: 1px solid var(--grayscale-divider, #DFE0EB);
  background: var(--grayscale-white, #FFF);
  padding: 20px;
}

.overview main .main_wrapper .card.dark2{
  background: black;
  color: white;
}

.overview main .main_wrapper .card p {
  color: var(--grayscale-gray, #9FA2B4);
  text-align: center;
  font-family: Mulish;
  font-size: 19px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.4px;
}
.overview main .main_wrapper .card h1 {
  color: var(--grayscale-black, #252733);
  text-align: center;
  font-family: Mulish;
  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 1px;
}

.overview main .main_wrapper .card:hover {
  border: 1px solid var(--accent-default, #3751FF);
  cursor: pointer;
}
.overview main .main_wrapper .card:hover p {
  color: #3751FF;
}
.overview main .main_wrapper .card:hover h1 {
  color: #3751FF;
}
.overview section {
  width: 100%;
}
.overview section .section_wrapper {
  max-width: 1122px;
  margin: auto;
  display: flex;
  border-radius: 8px;
  border: 1px solid var(--grayscale-divider, #DFE0EB);
  background: var(--grayscale-white, #FFF);
}

.overview section .section_wrapper.dark{
  background: black;
  color: white;
}

.overview section .section_wrapper .wrapper1 {
  width: 70%;
  padding: 1rem;
}
.overview section .section_wrapper .wrapper1 h2 {
  color: var(--grayscale-black, #252733);
  font-family: Mulish;
  font-size: 19px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.4px;
}
.overview section .section_wrapper .wrapper1 .wrapper_card {
  width: 100%;
  display: flex;
}
.overview section .section_wrapper .wrapper1 .wrapper_card .card1 {
  width: 50%;
}
.overview section .section_wrapper .wrapper1 .wrapper_card .card1 p {
  color: var(--grayscale-gray, #9FA2B4);
  font-family: Mulish;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.1px;
  padding: 10px 0;
}
.overview section .section_wrapper .wrapper1 .wrapper_card .card2 {
  width: 50%;
  text-align: right;
}
.overview section .section_wrapper .wrapper1 .wrapper_card .card2 p {
  display: inline;
  color: var(--grayscale-gray, #9FA2B4);
  font-family: Mulish;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.1px;
  padding: 10px 0;
}
.overview section .section_wrapper .wrapper2 {
  width: 30%;
}
.overview section .section_wrapper .wrapper2 .card {
  width: 100%;
  text-align: center;
  padding: 2rem;
  border: 1px solid var(--grayscale-divider, #DFE0EB);
}
.overview section .section_wrapper .wrapper2 .card p {
  color: var(--grayscale-gray, #9FA2B4);
  text-align: center;
  font-family: Mulish;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0.3px;
  padding-bottom: 10px;
}
.overview section .section_wrapper .wrapper2 .card h2 {
  color: var(--grayscale-black, #252733);
  text-align: center;
  font-family: Mulish;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.3px;
}

